import React, { useState, useEffect } from 'react';
import {getDirectores} from '../../services/directorService';
import {getGeneros} from '../../services/generoService';
import {getProductoras} from '../../services/productoraService';
import {getTipos} from '../../services/tipoService';
import {createMedia} from '../../services/mediaService';
import Swal from 'sweetalert2';

export const MediaNew = ({handleOpenMedia, ListMedia}) => {

  const [directores, setDirectores] = useState([]);
  const [generos, setGeneros] = useState([]);
  const [productoras, setProductoras] = useState([]);
  const [tipos, setTipos] = useState([]);
  const [valoresForm, setValoresForm]= useState([]);
  const {title='', synopsis='', movieUrl='',coverImage='',
  releaseYear='',mainGenre, mainDirector,  producer, type}=valoresForm

  const listDirectores = async () => {
    try {
      const { data } = await getDirectores();
      setDirectores(data);

    } catch (error){
      console.log(error);
      
    }
  }

  useEffect(() => {
    listDirectores();
  }, []);

  const listGeneros = async () => {
    try {
      const { data } = await getGeneros();
      setGeneros(data);

    } catch (error){
      console.log(error);
      
    }
  }

  useEffect(() => {
    listGeneros();
  }, []);

  const listProductoras = async () => {
    try {
      const { data } = await getProductoras();
      setProductoras(data);

    } catch (error){
      console.log(error);
      
    }
  }

  useEffect(() => {
    listProductoras();
  }, []);

  const listTipos = async () => {
    try {
      const { data } = await getTipos();
      setTipos(data);

    } catch (error){
      console.log(error);
      
    }
  }

  useEffect(() => {
    listTipos();
  }, []);

  const handleOnChange = ({ target }) => {
    const { name, value } = target;
    setValoresForm({ ...valoresForm, [name]: value });
  }


  const handleOnSubmit = async (e) => {
    e.preventDefault();
    const media = {
      title, synopsis, movieUrl, coverImage, releaseYear,
      mainGenre:
      mainGenre,
      mainDirector:
      mainDirector,
      producer:
      producer,
      type: 
      type
    }
    console.log(media);

    try {

      Swal.fire({
        allowOutsideClick: false,
        text: 'Cargado...'
      });
      Swal.showLoading();
      const { data } = await createMedia(media);
      handleOpenMedia();
      ListMedia();
      Swal.close();

    } catch (error) {
      console.log(error);
      Swal.close();
    }
  }

  return (
    <div className='sideMedia'>
      <div className='container-fluid'>
        <div className='row'>
          <div className='col'>
            <div className='sideMedia-header'>
              <h2>Crear Media</h2>
              <i className="fa-solid fa-xmark" onClick={handleOpenMedia}></i>
            </div>
          </div>
        </div>
        <div className='row'>
          <div className='col'>
            <hr />
          </div>
        </div>
        <form onSubmit={handleOnSubmit}>
      <div className='row'>
        <div className='col'>
          <div className="mb-3">
            <label className="form-label">Titulo</label>
            <input type="text" name='title'
              value={title}
              onChange={handleOnChange}
              required
              className="form-control" />
          </div>
        </div>

        <div className='col'>
          <div className="mb-3">
            <label className="form-label">Synopsys</label>
            <input type="text" name='synopsis'
              value={synopsis}
              onChange={handleOnChange}
              required
              className="form-control" />
          </div>
        </div>

        <div className='col'>
          <div className="mb-3">
            <label className="form-label">Url</label>
            <input type="url" name='movieUrl'
              value={movieUrl}
              onChange={handleOnChange}
              required
              className="form-control" />
          </div>
        </div>

        <div className='col'>
          <div className="mb-3">
            <label className="form-label">Portada</label>
            <input type="url" name='coverImage'
              value={coverImage}
              onChange={handleOnChange}
              required
              className="form-control" />
          </div>
        </div>
      </div>

      <div className='row'>
        <div className='col'>
          <div className="mb-3">
            <label className="form-label">Año</label>
            <input type="number" name='releaseYear'
              value={releaseYear}
              onChange={handleOnChange}
              required
              className="form-control" />
          </div>
        </div>

        <div className='col'>
          <div className="mb-3">
            <label className="form-label">Genero</label>
            <select className='form-select'
              required
              name='mainGenre'
              value={valoresForm.mainGenre}
              onChange={handleOnChange}>
              <option value="">--SELECCIONE--</option>
              {generos.map(({ _id, name }) => (
                <option key={_id} value={_id}>{name}</option>
              ))}
            </select>
          </div>
        </div>

        <div className='col'>
          <div className="mb-3">
            <label className="form-label">Director</label>
            <select className='form-select'
              required
              name='mainDirector'
              value={valoresForm.mainDirector}
              onChange={handleOnChange}>
              <option value="">--SELECCIONE--</option>
              {directores.map(({ _id, name }) => (
                <option key={_id} value={_id}>{name}</option>
              ))}
            </select>
          </div>
        </div>

        <div className='col'>
          <div className="mb-3">
            <label className="form-label">Productora</label>
            <select className='form-select'
              required
              name='producer'
              value={valoresForm.producer}
              onChange={handleOnChange}>
              <option value="">--SELECCIONE--</option>
              {productoras.map(({ _id, name }) => (
                <option key={_id} value={_id}>{name}</option>
              ))}
            </select>
          </div>
        </div>

        <div className='col'>
          <div className="mb-3">
            <label className="form-label">Tipo</label>
            <select className='form-select'
              required
              name='type'
              value={valoresForm.type}
              onChange={handleOnChange}>
              <option value="">--SELECCIONE--</option>
              {tipos.map(({ _id, name }) => (
                <option key={_id} value={_id}>{name}</option>
              ))}
            </select>
          </div>
        </div>
      </div>

      <div className='row'>
        <div className='col'>
          <button type="submit" className="btn btn-primary">Guardar</button>
        </div>
      </div>
    </form>
   </div>
  </div>  
  );
};


