import React, {useState, useEffect} from 'react'
import {TipoCard} from './TipoCard'
import {TipoNew} from './TipoNew'
import {TipoUpdate} from './TipoUpdate'
import { getTipos } from '../../services/tipoService' 

export const TipoView = () => {

  const [tipos, setTipos] = useState ([]);
  const [newTipo, setNewTipo] = useState(false)
  const [updateTipo, setUpdateTipo] = useState(false)


  const getListTipos= async () => {

    try {

      const { data } = await getTipos();
      setTipos(data);

    }catch(error) {
      console.log(error);
    }
  }

  const handleNewTipo = async() => {

    setNewTipo(!newTipo)
  }


  const handleUpdateTipo = async() => {

    setUpdateTipo(!updateTipo)
  }

  useEffect (() => {
    getListTipos();
  }, []);


  const ContainerButtons = () => {


    return (
        <div className="button-container">
          <button className="btn btn-dark new_tipo" onClick={handleNewTipo}>
            <i className="fa-solid fa-plus"></i>
          </button>

          <button className="btn btn-dark update_tipo" onClick={handleUpdateTipo}>
              <i className="fa-solid fa-edit"></i>
          </button>
        </div>
      )
  }

    return (

        <div className="container mt-2 mb-2">
          <div className="mt-2 mb-2 row row-cols-2 row-cols-md-4 g-4">
            {tipos.map((tipo) => (
              <TipoCard key={tipo.id} tipo={tipo} />
            ))}
          </div>

          <div>
            {newTipo ? <TipoNew handleNewTipo={handleNewTipo} getListTipos={getListTipos} tipos={tipos} />
             : < ContainerButtons/>}

            {updateTipo ? <TipoUpdate handleUpdateTipo={handleUpdateTipo} getListTipos={getListTipos} tipos={tipos} />
             : < ContainerButtons/>}
          </div>
      </div>

    )
}

