import React, {useState, useEffect} from 'react'
import {ProductoraCard} from './ProductoraCard'
import {ProductoraNew} from './ProductoraNew'
import {ProductoraUpdate} from './ProductoraUpdate'
import { getProductoras } from '../../services/productoraService' 

export const ProductoraView = () => {

  const [productoras, setproductoras] = useState ([]);
  const [newProductora, setNewProductora] = useState(false)
    const [updateProductora, setUpdateProductora] = useState(false)


  const getListProductoras= async () => {

    try {

      const { data } = await getProductoras();
      setproductoras(data);

    }catch(error) {
      console.log(error);
    }
  }

  const handleNewProductora = async() => {

    setNewProductora(!newProductora)
  }


  const handleUpdateProductora = async() => {

    setUpdateProductora(!updateProductora)
  }

  useEffect (() => {
    getListProductoras();
  }, []);


  const ContainerButtons = () => {


    return (
        <div className="button-container">
          <button className="btn btn-dark new_productora" onClick={handleNewProductora}>
            <i className="fa-solid fa-plus"></i>
          </button>

          <button className="btn btn-dark update_productora" onClick={handleUpdateProductora}>
              <i className="fa-solid fa-edit"></i>
          </button>
        </div>
      )
  }

    return (

        <div className="container mt-2 mb-2">
          <div className="mt-2 mb-2 row row-cols-2 row-cols-md-4 g-4">
            {productoras.map((productora) => (
              <ProductoraCard key={productora.id} productora={productora} />
            ))}
          </div>

          <div>
            {newProductora ? <ProductoraNew handleNewProductora={handleNewProductora} getListProductoras={getListProductoras} productoras={productoras} />
             : < ContainerButtons/>}

            {updateProductora ? <ProductoraUpdate handleUpdateProductora={handleUpdateProductora} getListProductoras={getListProductoras} productoras={productoras} />
             : < ContainerButtons/>}
          </div>
      </div>

    )
}

