import React, {useState} from 'react'
import Swal from 'sweetalert2';
import {createTipos} from '../../services/tipoService'


export const TipoNew = ({handleNewTipo, getListTipos}) => {

	const [valuesForm, setValuesForm] = useState([])
	const{name='', description='', coverImage=''} = valuesForm


	const handleOnChange = ({ target }) => {

    	const { name, value } = target;

    	setValuesForm({ ...valuesForm, [name]: value });
  	}

  	const handleOnSubmit = async (e) => {

    	e.preventDefault();

      
    	await createTipos(valuesForm);

	   	try {

	    	Swal.fire({
	        	allowOutsideClick: false,
	        	text: 'Cargado...'
	      	});

	      	Swal.showLoading();  
	      	handleNewTipo();
	      	getListTipos()
	      	Swal.close();

	    } catch (error) {
	    	console.log(error);
	    	Swal.close();
	    }
  	}

  return (
    <div className='sideMedia'>
      <div className='container-fluid'>
        <div className='row'>
          <div className='col'>
            <div className='sideMedia-header'>
              <h2>Crear Tipo Media</h2>
              <i className="fa-solid fa-xmark" onClick={handleNewTipo}></i>
            </div>
          </div>
        </div>
        <div className='row'>
          <div className='col'>
            <hr />
          </div>
        </div>
        <form onSubmit={(e) => handleOnSubmit(e)}>
          <div className='row'>
            <div className='col'>
              <div className="mb-4">
                <label className="form-label">Nombre</label>
                <input type="text" name='name'
                  value={name}
                  onChange={e => handleOnChange(e)}
                  required
                  className="form-control" />
              </div>
            </div>
          </div>
           <div className='row'>
            <div className='col'>
              <div className="mb-4">
                <label className="form-label">URL de Imagen</label>
                <input type="text" name='coverImage' value={coverImage} onChange={e => handleOnChange(e)}
                 required 
                 className="form-control" />
              </div>
            </div>
          </div>
          <div className='row'>
            <div className='col'>
              <div className="mb-4">
                <label className="form-label">Descripción</label>
                <textarea name='description'
                  value={description}
                  onChange={e => handleOnChange(e)}
                  required
                  className="form-control" />
              </div>
            </div>
          </div>
          <div className='row'>
            <div className='col'>
              <button className="btn btn-primary">Guardar</button>
            </div>
          </div>
        </form>
      </div>
    </div>
  )

}




