import React, {useState, useEffect } from 'react';
import Swal from 'sweetalert2';
import { useParams, useHistory } from 'react-router-dom';
import { updateMedia, getMediaForSerial} from '../../services/mediaService';

export const MediaUpdate = () => {
  
  const history = useHistory(); 
  const {serialId} = useParams();

  let [valuesForm, setValuesForm] = useState([])

  const initializeField = async ()=> {

    const {data} = await getMediaForSerial(serialId)
    setValuesForm(data)
    console.log('datos para inicializar',data )

  }

  const handleUpdateMedia = () => {
    history.push('/media'); // Redirigir correctamente
  };


  const handleOnChange = ({ target }) => {
    const { name, value } = target;
    setValuesForm((prev) => ({ ...prev, [name]: value }));
  };

  const handleOnSubmit = async (e) => {
    e.preventDefault();

    try {
      Swal.fire({
        allowOutsideClick: false,
        text: 'Cargando...',
      });

      Swal.showLoading();
      await updateMedia(valuesForm.serial, valuesForm);
      Swal.close();

      handleUpdateMedia();

    } catch (error) {
      console.log(error);
      Swal.close();
    }
  };

  useEffect (() => {
    initializeField();
  }, [])



  return (
    <div className="sideMedia">
      <div className="container-fluid">
        <div className="row">
          <div className="col">
            <div className="sideMedia-header">
              <h2>Actualizar Media</h2>
              <i className="fa-solid fa-xmark" onClick={handleUpdateMedia}></i>
            </div>
          </div>
        </div>
        <div className="row">
          <div className="col">
            <hr />
          </div>
        </div>
        <form onSubmit={handleOnSubmit}>
          <div className="row">
            <div className="col">
              <div className="mb-4">
                <label className="form-label">Titulo</label>
                <input
                  type="text"
                  name="title"
                  value={valuesForm.title}
                  onChange={handleOnChange}
                  required
                  className="form-control"
                />
              </div>
            </div>
          </div>
           <div className="row">
            <div className="col">
              <div className="mb-4">
                <label className="form-label">Synopsis</label>
                <textarea
                  name="synopsis"
                  value={valuesForm.synopsis}
                  onChange={handleOnChange}
                  required
                  className="form-control"
                />
              </div>
            </div>
          </div>

          <div className="row">
            <div className="col">
              <div className="mb-4">
                <label className="form-label">Url</label>
                <input
                  type="text"
                  name="movieUrl"
                  value={valuesForm.movieUrl}
                  onChange={handleOnChange}
                  required
                  className="form-control"
                />
              </div>
            </div>
          </div>
          <div className="row">
            <div className="col">
              <div className="mb-4">
                <label className="form-label">Portada</label>
                <input
                  type="url"
                  name="coverImage"
                  value={valuesForm.coverImage}
                  onChange={handleOnChange}
                  required
                  className="form-control"
                />
              </div>
            </div>
          </div>
         <div className="row">
            <div className="col">
              <div className="mb-4">
                <label className="form-label">Año de lanzamiento</label>
                <input
                  type="number"
                  name="releaseYear"
                  value={valuesForm.releaseYear}
                  onChange={handleOnChange}
                  required
                  className="form-control"
                />
              </div>
            </div>
          </div>
          <div className="row">
            <div className="col">
              <button className="btn btn-primary">Guardar</button>
            </div>
          </div>
        </form>
      </div>
    </div>
  );
};
