import React, {useState, useEffect} from 'react'
import {DirectorCard} from './DirectorCard'
import {DirectorNew} from './DirectorNew'
import {DirectorUpdate} from './DirectorUpdate'
import { getDirectores } from '../../services/directorService' 

export const DirectorView = () => {

  const [directores, setDirectores] = useState ([]);
  const [newDirector, setNewDirector] = useState(false)
    const [updateDirector, setUpdateDirector] = useState(false)


  const getListDirectores= async () => {

    try {

      const { data } = await getDirectores();
      setDirectores(data);

    }catch(error) {
      console.log(error);
    }
  }

  const handleNewDirector = async() => {

    setNewDirector(!newDirector)
  }


  const handleUpdateDirector = async() => {

    setUpdateDirector(!updateDirector)
  }

  useEffect (() => {
    getListDirectores();
  }, []);


  const ContainerButtons = () => {


    return (
        <div className="button-container">
          <button className="btn btn-dark new_director" onClick={handleNewDirector}>
            <i className="fa-solid fa-plus"></i>
          </button>

          <button className="btn btn-dark update_director" onClick={handleUpdateDirector}>
              <i className="fa-solid fa-edit"></i>
          </button>
        </div>
      )
  }

    return (

        <div className="container mt-2 mb-2">
          <div className="mt-2 mb-2 row row-cols-2 row-cols-md-4 g-4">
            {directores.map((director) => (
              <DirectorCard key={director.id} director={director} />
            ))}
          </div>

          <div>
            {newDirector ? <DirectorNew handleNewDirector={handleNewDirector} getListDirectores={getListDirectores} directores={directores} />
             : < ContainerButtons/>}

            {updateDirector ? <DirectorUpdate handleUpdateDirector={handleUpdateDirector} getListDirectores={getListDirectores} directores={directores} />
             : < ContainerButtons/>}
          </div>
      </div>

    )
}


