import {axiosInstance} from '../helper/axios-config';

const getMedia = () => {
    return axiosInstance.get('media', {
        headers: {
            'Content-type': 'application/json'
        }
    });
}

const createMedia = (data) => {
    return axiosInstance.post('media', data, {
        headers: {
            'Content-type': 'application/json'
        }
    });
}

const updateMedia = (serialId, data) => {
    return axiosInstance.put(`media/${serialId}`, data, {
        headers: {
            'Content-type': 'application/json'
        }
    });
}

const getMediaForSerial = (serialId) => {
       return axiosInstance.get(`media/${serialId}`, {
           headers: {
               'Content-type': 'application/json'
           }
       });
   }

export {
    getMedia, createMedia, updateMedia, getMediaForSerial
}