
 export const GenreCard = (props) => {

 	const {genre} = props;

 	return (

	 	<div className="col d-flex">
	      <div className="card h-auto">
	     	  <img src={genre.coverImage} className="card-img-top img-fluid" alt="..." style={{ width: '100%', height: '250px', objectFit: 'cover' }} />
	        <div className="card-body">
		         <p className="card-text">{`${genre.name}`}</p>
		         <p className="card-text">{`${genre.description}`}</p>
		         <p className="card-text">{`Id: ${genre._id}`}</p>
		         <p className="card-text">{`Fecha de creacion: ${new Date(genre.createdAt).toLocaleDateString()}`}</p>
		         <p className="card-text">{`Fecha de actualizacion: ${new Date(genre.updatedAt).toLocaleDateString()}`}</p>
		         <p className="card-text">{`Estado: ${genre.state}`}</p>

	        </div>
	      </div>
	    </div>
 	)
 }