import React, {useState, useEffect} from 'react'
import {GenreCard} from './GeneroCard'
import {GenreNew} from './GeneroNew'
import {GenreUpdate} from './GeneroUpdate'
import { getGeneros } from '../../services/generoService' 

export const GeneroView = () => {

  const [genres, setGenres] = useState ([]);
  const [newGenre, setNewGenre] = useState(false)
    const [updateGenre, setUpdateGenre] = useState(false)


  const getListGenres= async () => {

    try {

      const { data } = await getGeneros();
      setGenres(data);

    }catch(error) {
      console.log(error);
    }
  }

  const handleNewGenre = async() => {

    setNewGenre(!newGenre)
  }


  const handleUpdateGenre = async() => {

    setUpdateGenre(!updateGenre)
  }

  useEffect (() => {
    getListGenres();
  }, []);


  const ContainerButtons = () => {


    return (
        <div className="button-container">
          <button className="btn btn-dark new_genre" onClick={handleNewGenre}>
            <i className="fa-solid fa-plus"></i>
          </button>

          <button className="btn btn-dark update_genre" onClick={handleUpdateGenre}>
              <i className="fa-solid fa-edit"></i>
          </button>
        </div>
      )
  }

    return (

        <div className="container mt-2 mb-2">
          <div className="mt-2 mb-2 row row-cols-2 row-cols-md-4 g-4">
            {genres.map((genre) => (
              <GenreCard key={genre.id} genre={genre} />
            ))}
          </div>

          <div>
            {newGenre ? <GenreNew handleNewGenre={handleNewGenre} getListGenres={getListGenres} genres={genres} />
             : < ContainerButtons/>}

            {updateGenre ? <GenreUpdate handleUpdateGenre={handleUpdateGenre} getListGenres={getListGenres} genres={genres} />
             : < ContainerButtons/>}
          </div>
      </div>

    )
}

