import React from 'react'
import {Link} from 'react-router-dom';


export const MediaCard = (props) => {

    const { media } = props;
  return (
    <div className="col">
      <div className="card">
      <img src={media.coverImage} className="card-img-top img-fluid" alt="..." style={{ width: '180%', height: '380px', objectFit: 'cover' }} />
        <div className="card-body">
         <h5 className="card-title">Descripcion</h5>
         <hr/>
         <p className="card-text">{`Serial: ${media.serial}`}</p>
         <p className="card-text">{`${media.title}`}</p>
         <p className="card-text">{`Synopsys: ${media.synopsis}`}</p>
         <p className="card-text">{`${media.movieUrl}`}</p>
        <p className="card-text">{`Fecha de creacion: ${new Date(media.createdAt).toLocaleDateString()}`}</p>
         <p className="card-text">{`Fecha de actualizacion: ${new Date(media.updatedAt).toLocaleDateString()}`}</p>
         <p className="card-text">{`Año: ${media.releaseYear}`}</p>
         <p className="card-text">{`Genero: ${media.mainGenre.name}`}</p>
         <p className="card-text">{`Director: ${media.mainDirector.name}`}</p>
         <p className="card-text">{`Productora: ${media.producer.name}`}</p>
         <p className="card-text">{`Tipo: ${media.type.name}`}</p>
        </div>
        <Link to = {`media/edit/${media.serial}`} className="btn btn-dark update_media" >
           <i className="fa-solid fa-edit"></i>
        </Link>
      </div>
    </div>
  )
}

