
 export const TipoCard = (props) => {

 	const {tipo} = props;

 	return (

	 	<div className="col d-flex">
	      <div className="card h-auto">
	     	  <img src={tipo.coverImage} className="card-img-top img-fluid" alt="..." style={{ width: '100%', height: '250px', objectFit: 'cover' }} />
	        <div className="card-body">
		         <p className="card-text">{`${tipo.name}`}</p>
		         <p className="card-text">{`${tipo.description}`}</p>
		         <p className="card-text">{`Id: ${tipo._id}`}</p>
		         <p className="card-text">{`Fecha de creacion: ${new Date(tipo.createdAt).toLocaleDateString()}`}</p>
		         <p className="card-text">{`Fecha de actualizacion: ${new Date(tipo.updatedAt).toLocaleDateString()}`}</p>
	        </div>
	      </div>
	    </div>
 	)
 }