
 export const DirectorCard = (props) => {

 	const {director} = props;

 	return (

	 	<div className="col d-flex">
	      <div className="card h-auto">
	     	  <img src={director.coverImage} className="card-img-top img-fluid" alt="..." style={{ width: '100%', height: '250px', objectFit: 'cover' }} />
	        <div className="card-body">
		         <p className="card-text">{`${director.name}`}</p>
		         <p className="card-text">{`Id: ${director._id}`}</p>
		         <p className="card-text">{`Fecha de creacion: ${new Date(director.createdAt).toLocaleDateString()}`}</p>
		         <p className="card-text">{`Fecha de actualizacion: ${new Date(director.updatedAt).toLocaleDateString()}`}</p>
		         <p className="card-text">{`Estado: ${director.state}`}</p>

	        </div>
	      </div>
	    </div>
 	)
 }