
 export const ProductoraCard = (props) => {

 	const {productora} = props;

 	return (

	 	<div className="col d-flex">
	      <div className="card h-auto">
	     	  <img src={productora.coverImage} className="card-img-top img-fluid" alt="..." style={{ width: '100%', height: '250px', objectFit: 'cover' }} />
	        <div className="card-body">
		         <p className="card-text">{`${productora.name}`}</p>
		         <p className="card-text">{`${productora.description}`}</p>
		         <p className="card-text">{`Id: ${productora._id}`}</p>
		         <p className="card-text">{`Fecha de creacion: ${new Date(productora.createdAt).toLocaleDateString()}`}</p>
		         <p className="card-text">{`Fecha de actualizacion: ${new Date(productora.updatedAt).toLocaleDateString()}`}</p>
		         <p className="card-text">{`Estado: ${productora.state}`}</p>

	        </div>
	      </div>
	    </div>
 	)
 }